<template>
	<pubpage :navigationHasBottomLine="false">
		<view slot="contentSection">
			<view class="User">
				<image @click="updateUserProfile" :src="userInfo.avatarUrl?userInfo.avatarUrl:'/static/unlogin_avatarUrl.png'" mode="widthFix"></image>
				<view class="User_text">
					<view>{{userInfo.nickName?userInfo.nickName:'昵称'}}</view>
					<view v-if="!isLogin"><text class="np-tag">点击头像同步微信信息</text></view>
				</view>
			</view>
			
			<!-- <view v-if="!isLogin" @click="updateUserProfile" class="np-blockbutton">授权登录</view>
			<view v-if="isLogin" @click="loginUserProfile" class="np-blockbutton">个人中心</view>
			<view v-if="isLogin" @click="logOutUserProfile" class="np-blockbutton">退出登录</view> -->
			
			<!-- <view class="tips">Welcome to here！</view> -->
		</view>
	</pubpage>
</template>

<script>
	import loginUser from "../../common/currentUser.js"	
	import pubpage from "../../components/pubpage.vue"
	import { mapState,mapMutations,mapActions} from 'vuex'
	
	export default {
		components:{
			pubpage
		},
		data() {
			// unlogin_avatarUrl = require('/static/unlogin_avatarUrl.png')
			return {
				// userInfo:null,
				isLogin:false
			}
		},
		computed:{
		   ...mapState(['userInfo']),
		},
		async onLoad(options){
			// this.setUserInfo(await loginUser.login())			
			// console.log('登录token返回',this.userInfo)
			// if(this.userInfo.avatarUrl){
			// 	this.isLogin = true;
			// }
			
			// if(options.scene){
			// 	var scene = unescape(options.scene);
			// 	var params = scene.split("=");
			// 	var key = params[0];
				
			// 	uni.navigateTo({
			// 		url:"../bookshelf/bookshelf?id="+params[1]
			// 	})
			// }
		},
		onShow() {
			
		},
		methods: {
			...mapMutations(['setUserInfo']),
			// ...mapActions(['getUserInfo']),
			updateUserProfile(){
				uni.getUserProfile({
					desc: '信息给哥交出来',
					success: (res) => {
						this.setUserInfo(
							Object.assign(this.userInfo,res.userInfo)
						);
						loginUser.updateUserInfo(this.userInfo);
						this.isLogin = true;
					}
				})
			},
			loginUserProfile(){
				uni.navigateTo({
					url:"../person/person"
				})
			},
			logOutUserProfile(){
				this.setUserInfo({})	
				this.isLogin = false
				console.log('退出登录',this.userInfo)
			}
		}
	}
</script>

<style lang="less" scoped>
	.User{
		background-color: #fff;
		position: relative;
		display: flex; 
		margin: 10rpx 0;
		image{
			width:200rpx;
			height:200rpx;
			border-radius: 50rpx;
			// background-color: #8F8F94;
		}
		.User_text{
			display: flex;
			flex-direction: column;
			justify-content: center;
			margin-left: 10rpx;
			view{
				margin: 10rpx 0;
			}
			.np-tag{
				padding: 0 5px;
				border-radius: 5px;
				background-color: #00aaff;
			}
		}

		.np-blockbutton{
			margin: 20px auto ;
			width: 90%;
		}
	}
	.tips{
		text-align: center;
		margin-top: 50px;
		font-size: 20px;
	}
</style>
